<template>
  <div class="tags-content">
    <el-card class="box-card">
      <!-- 搜索框 -->
      <el-row>
        <el-col :span="18">
          <el-form
            :model="searchForm"
            size="small"
            style="display: flex"
            ref="searchFormDom"
          >
            <el-form-item label="关键字" label-width="80px">
              <el-input v-model="searchForm.keyword"></el-input>
            </el-form-item>
            <el-form-item label="状态" label-width="80px">
              <el-select v-model="searchForm.state" placeholder="请选择">
                <el-option label="启用" value="1"></el-option>
                <el-option label="禁用" value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item style="margin-left: 10px">
              <el-button @click="searchForm.keyword = searchForm.state = ''"
                >清除</el-button
              >
              <el-button type="primary" @click="secrchClick">搜索</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="6" style="text-align: right">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="small"
            @click="open('add')"
            >新增技巧</el-button
          >
        </el-col>
      </el-row>
      <el-alert
        :title="alertTitle"
        type="info"
        :closable="false"
        show-icon
        v-if="alertTitle !== ''"
      ></el-alert>
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号" width="80"></el-table-column>
        <el-table-column prop="title" label="文章标题" width="400">
          <template #default="{ row }">
            <span>{{ row.title }}</span>
            <span v-if="row.videoURL">
              <a href="javascript:void(0);" @click="openVideo(row.id)">
                <i class="el-icon-film"></i>
              </a>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="visits" label="阅读数"></el-table-column>
        <el-table-column prop="username" label="录入人"></el-table-column>
        <el-table-column prop="addDate" label="录入时间">
          <template #default="{ row }">
            <span>{{ row.createTime | parseTimeByString }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="状态">
          <template #default="{ row }">
            <span>{{ row.state === 1 ? "已启用" : "已禁用" }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template #default="{ row }">
            <el-button type="text" size="small" @click="open('look', row)"
              >预览</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="disableClick(row.state, row.id)"
            >
              <template #default v-if="row.state === 1"> 禁用 </template>
              <template #default v-else>启用 </template>
            </el-button>
            <el-button
              type="text"
              size="small"
              :disabled="row.state === 1"
              @click="open('edit', row.id)"
              >修改</el-button
            >
            <el-button
              type="text"
              size="small"
              :disabled="row.state === 1"
              @click="del(row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageObj.page"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="10"
        layout=" prev, pager, next, sizes,jumper"
        :total="total"
        hide-on-single-page
      >
      </el-pagination>
      <!-- 对话框 -->
      <el-dialog
        :title="titleText"
        :visible.sync="dialogFormVisible"
        width="800px"
        :before-close="cancel"
      >
        <div class="preview" v-if="typeDialog === 'look'">
          <div class="title">
            <h2>{{ articleDetail.title }}</h2>
            <div class="time">
              <span>{{ articleDetail.createTime | parseTimeByString }}</span>
              <span>{{ articleDetail.username }}</span>
              <span class="el-icon-view"></span>
              <span>{{ articleDetail.visits }}</span>
            </div>
          </div>
          <div class="content">
            <div v-html="articleDetail.articleBody"></div>
          </div>
        </div>
        <el-form
          :model="addArticleForm"
          label-width="100px"
          ref="addFormDom"
          :rules="ruleForm"
          v-else
        >
          <el-form-item label="文章标题" prop="title">
            <el-input
              placeholder="请输入文章标签"
              v-model.trim="addArticleForm.title"
            ></el-input>
          </el-form-item>
          <el-form-item label="文章内容" prop="articleBody">
            <quill-editor
              v-model.trim="addArticleForm.articleBody"
              ref="myQuillEditor"
              :options="editorOption"
              class="quillAdd"
            >
            </quill-editor>
          </el-form-item>
          <el-form-item label="视频地址" prop="videoURL">
            <el-input
              placeholder="请输入视频地址"
              v-model="addArticleForm.videoURL"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer" v-if="typeDialog !== 'look'">
          <el-button @click="cancel">取 消</el-button>
          <el-button type="primary" @click="determine">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 视频蒙层 -->
      <div class="view-video" v-if="isVideoShow">
        <div class="close" @click="isVideoShow = false">
          <i class="el-icon-close"></i>
        </div>
        <div class="box">
          <video
            :src="videoUrlSrc"
            style="width: 100%; height: 100%"
            controls
          ></video>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import {
  list,
  changeState,
  add,
  detail,
  update,
  remove
} from '../../api/hmmm/articles'

import PageTool from '../../module-manage/components/page-tool.vue'
export default {
  comments: {
    PageTool
  },
  computed: {
    titleText () {
      let txt = ''
      if (this.typeDialog === 'add') {
        txt = '新增文章'
      } else if (this.typeDialog === 'edit') {
        txt = '修改文章'
      } else {
        txt = '预览文章'
      }
      return txt
    }
  },
  data () {
    const toolbarOptions = [
      // 工具栏想要显示什么，就要在这里加上什么
      ['bold', 'italic', 'underline', 'strike'], // 粗体、斜体、下划线、删除线
      [{ list: 'ordered' }, { list: 'bullet' }], // 有序列表、无序列表
      ['blockquote'],
      ['code-block', 'image', 'link'] // 链接、图片、视频
    ]
    return {
      tableData: [],
      isVideoShow: false, // 视频蒙层是否显示
      videoUrlSrc: '', // 视频地址
      alertTitle: '',
      total: 0,
      dialogFormVisible: false,
      updateId: '', // 文章id
      typeDialog: '',
      pageObj: {
        page: 1, // 当前页数
        pagesize: 10 // 每页数据
      },
      searchForm: {
        keyword: '', // 关键字
        state: '' // 状态 1 开启 0 屏蔽
      },
      rules: {
        tagName: [
          { required: true, message: '请输入标签名称', trigger: 'blur' },
          {
            min: 1,
            max: 16,
            message: '长度在 1 到 16 个字符',
            trigger: 'blur'
          }
        ],
        subjectID: [
          { required: true, message: '请选择所属学科', trigger: 'change' }
        ]
      },
      // 添加文章表单
      addArticleForm: {
        title: '',
        articleBody: '',
        videoURL: ''
      },
      ruleForm: {
        title: [
          { required: true, message: '请输入文章标题', trigger: 'blur' },
          {
            min: 1,
            max: 16,
            message: '长度在 1 到 16 个字符',
            trigger: 'blur'
          }
        ],
        articleBody: [
          { required: true, message: '请输入文章内容', trigger: 'blur' }
        ]
      },
      // 富文本编辑器配置项
      editorOption: {
        placeholder: '',
        modules: {
          toolbar: {
            container: toolbarOptions // 工具栏
          }
        }
      },
      articleDetail: {}
    }
  },
  async created () {
    this.getArtList(this.pageObj)
  },
  methods: {
    // 视频播放
    openVideo (id) {
      this.isVideoShow = true
      this.videoUrlSrc = this.tableData.find((item) => item.id === id).videoURL
    },
    // 修改状态
    async disableClick (state, id) {
      state = state ? 0 : 1
      await changeState({ id, state })
      this.$message.success('操作成功')
      this.getArtList(this.pageObj)
    },
    // 获取列表数据
    async getArtList (params) {
      const { data } = await list(params)
      this.tableData = data.items
      this.total = data.counts
      this.alertTitle = `数据一共 ${this.total} 条`
    },
    // 每页数据改变调用的函数
    handleSizeChange (val) {
      this.pageObj.pagesize = val
      this.pageObj.page = 1
      this.searchList()
    },
    // 页码改变的函数
    handleCurrentChange (val) {
      this.pageObj.page = val
      this.searchList()
    },
    // 打开对话框
    async open (type, id) {
      this.typeDialog = type
      this.updateId = id
      this.dialogFormVisible = true
      if (type === 'edit') {
        const { data: res } = await detail({ id })
        this.addArticleForm.title = res.title
        this.addArticleForm.articleBody = res.articleBody
        this.addArticleForm.videoURL = res.videoURL
      }
      if (type === 'look') {
        console.log(id)
        this.articleDetail = id
      }
    },
    // 对话框取消按钮
    cancel () {
      if (this.typeDialog !== 'look') this.$refs.addFormDom.resetFields()
      this.dialogFormVisible = false
    },
    // 对话框确认按钮
    async determine () {
      await this.$refs.addFormDom.validate()
      if (this.typeDialog === 'add') {
        await add(this.addArticleForm)
      } else if (this.typeDialog === 'edit') {
        await update({ id: this.updateId, ...this.addArticleForm })
      }
      this.$message.success('操作成功')
      this.dialogFormVisible = false
      this.getArtList(this.pageObj)
      this.$refs.addFormDom.resetFields()
    },
    // 删除
    del (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await remove({ id })
          this.$message.success('删除成功')
          this.getArtList(this.pageObj)
        })
        .catch(() => {})
    },
    // 搜索
    secrchClick () {
      this.pageObj.page = 1
      this.pageObj.pagesize = 10
      this.searchList()
    },
    searchList () {
      if (this.searchForm.keyword) {
        this.getArtList({
          ...this.pageObj,
          keyword: this.searchForm.keyword
        })
      } else if (this.searchForm.state) {
        this.getArtList({
          ...this.pageObj,
          state: this.searchForm.state
        })
      } else if (this.searchForm.keyword && this.searchForm.state) {
        this.getArtList({
          ...this.pageObj,
          ...this.searchForm
        })
      } else {
        this.getArtList(this.pageObj)
      }
    }
  }
}
</script>

<style scoped rel="stylesheet/scss" lang="scss">
.tags-content {
  padding: 10px;
  .el-form-item {
    font-size: 12px;
    .el-input__inner {
      width: 200px;
      height: 32px;
    }
  }
  .el-pagination {
    text-align: right;
    margin-top: 20px;
  }
  .dialog-footer {
    text-align: right;
  }
  ::v-deep .el-icon-film {
    color: #00f;
    font-size: 18px;
  }
  .view-video {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    overflow: hidden;
    z-index: 9999;
    .close {
      width: 50px;
      height: 50px;
      position: absolute;
      top: 30px;
      left: 50%;
      transform: translate(-50%);
      background: rgba(0, 0, 0, 0.4);
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
      border-radius: 50%;
      text-align: center;
      line-height: 50px;
      color: #fff;
      font-size: 20px;
    }
    .box {
      width: 800px;
      height: 600px;
      // background-color: #000;
      position: absolute;
      top: 50%;
      left: 50%;
      // opacity: 0.3;
      transform: translate(-50%, -50%);
    }
  }
  .quillAdd {
    ::v-deep .ql-editor {
      height: 200px;
    }
  }
  .el-dialog {
    .title {
      border-bottom: 1px dashed #ccc;
      padding-bottom: 10px;
      span {
        margin-left: 10px;
        margin-top: 10px;
      }
    }
    .content {
      background: #f5f5f5;
      padding: 10px;
      ::v-deep img {
        width: 100%;
      }
    }
  }
}
</style>
